.forgot-container {
    text-align: center;
    padding: 20px;
    font-family: "Courgette", cursive;
}

.forgot-title {
    font-size: 48px;
    color: #e63946;
    margin-bottom: 20px;
}

.forgot-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

.forgot-form input {
    width: 260px;
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 8px;
    font-size: 18px;
}

.forgot-button {
    background: #e63946;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
}

.forgot-error {
    color: red;
    font-size: 18px;
}

.forgot-success {
    color: green;
    font-size: 18px;
}