.login-container {
    text-align: center;
    padding: 20px;
    font-family: "Courgette", cursive;
}

.login-title {
    font-size: 48px;
    color: #e63946;
    margin-bottom: 20px;
}

.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
}

.login-form input {
    width: 260px;
    padding: 10px;
    border: 2px solid #ccc;
    border-radius: 8px;
    font-size: 18px;
}

.login-form input[type="checkbox"] {
    width: auto;
    padding: 0;
    margin-right: 8px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

.remember-me {
    font-size: 16px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 280px;
    justify-content: flex-start;
    cursor: pointer;
    color: #333;
}

.login-button {
    background: #e63946;
    color: white;
    border: none;
    padding: 12px 24px;
    font-size: 20px;
    border-radius: 10px;
    cursor: pointer;
}

.login-error {
    color: red;
    font-size: 18px;
}

.login-success {
    color: green;
    font-size: 18px;
}

.login-register-text {
    margin-top: 20px;
    font-size: 18px;
    font-family: serif;
}

.login-register-text a {
    color: rgb(0, 110, 255);
    font-size: 18px;
}

.login-register-text a:hover {
    text-decoration: underline;
}

.show-password-toggle {
    font-size: 16px;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 280px;
    justify-content: flex-start;
    cursor: pointer;
    color: #333;
}

.forgot-password-text {
    margin-top: 20px;
    font-size: 18px;
    font-family: serif;
}

.forgot-password-text a {
    color: rgb(0, 110, 255);
    font-size: 18px;
}

.forgot-password-text a:hover {
    text-decoration: underline;
}